import { Action } from '@ali/4ever-cangjie';
import Table from '../../mo/models';

export const TABLE_SELECT_TABLE = Symbol('TABLE_SELECT_TABLE');

export function selectTable(table) {
  return {
    type: TABLE_SELECT_TABLE,
    payload: {
      node: table,
    },
  };
}

interface TableSelectTablePayload {
  node: Table;
  startIndex: number;
  endIndex: number;
}

interface TableSelectTableAction {
  type: typeof TABLE_SELECT_TABLE;
  payload: TableSelectTablePayload;
}

export function isTableSelectTableAction(
  action: Action,
): action is TableSelectTableAction {
  return action.type === TABLE_SELECT_TABLE;
}
